nav {
  position: fixed;
  height: 100%;
  max-height: 900px;
  width: 200px;
  display: flex;
  flex-direction: column;
  z-index: 10;
  opacity: 0;
  animation: nav_slide_right 0.3s 0.5s ease-in forwards;

  ul {
    padding: 40px 40px 40px 0px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    li {
      justify-self: flex-start;
      margin-left: -15px;
      padding: 10px 40px;
      transition: opacity 0.3s ease-in-out;
      opacity: 0.25;

      a {
        font-size: 18px;
        font-weight: 500;
        color: $c-primary;
      }

      &:hover {
        opacity: 1;
      }

      &.active {
        opacity: 1;
      }
    }
  }

  @media (max-width: 1050px) {
    height: 80px;
    width: 100%;
    background: $c-background;
    z-index: 12;
    animation: nav_slide_down 0.3s 0.5s ease-in forwards;

    ul {
      padding: 0px;
      height: 100px;
      flex-direction: row;
      justify-content: center;
      align-items: center;

      li {
        margin-left: 0;
        padding: 10px 3.5%;
        opacity: 0.5;

        a {
          font-size: 17px;
        }

        &:hover {
          opacity: 0.5;
        }

        &.active {
          opacity: 1;
        }
      }
    }
  }

  @media (max-width: 400px) {
    ul li a {
      font-size: 15px;
    }
  }
}

@keyframes nav_slide_right {
  0% {
    opacity: 0;
    transform: translateX(-0.5rem);
  }

  100% {
    opacity: 1;
    transform: translateX(0rem);
  }
}

@keyframes nav_slide_down {
  0% {
    opacity: 0;
    transform: translateY(-0.5rem);
  }

  100% {
    opacity: 1;
    transform: translateY(0rem);
  }
}
